<template>
	<div id="statcenter-takeout">
		<public-header title="订单来源统计"></public-header>
		<div class="content">
			<div class="padding-15">
				<span class="font-16">{{store.title ? store.title : '全部店铺'}}</span>
				<span class="c-gray font-14">{{filter.items && filter.items.stat_day ? filter.items.stat_day.start + '-' + filter.items.stat_day.end : '今天'}}</span>
			</div>
			<van-cell-group>
				<van-cell title="总订单" :value="stat.total_success_order" />
				<van-cell title="总饿了么订单数" :value="stat.total_eleme_order" />
				<van-cell title="总美团订单数" :value="stat.total_meituan_order" />
				<van-cell title="总小程序订单数" :value="stat.total_wxapp_order" />
				<van-cell title="总H5订单数" :value="stat.total_wap_order" />
				<van-cell title="总顾客APP订单数" :value="stat.total_h5app_order" />
				<van-cell title="总后台创建订单数" :value="stat.total_plateformCreate_order" />
			</van-cell-group>
			<div class="padding-10">
				<van-button type="primary" size="normal" block class="font-16" @click="util.jsUrl('pages/statcenter/takeoutOrderChannelDetail')">查看详情</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				store: {}
			}
		},
		methods: {
			...mapMutations([
				'setSearch'
			]),

			onLoad() {
				let that = this;
				getData({
					vue: that,
					data: {
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/takeoutOrderChannel/index',
					success: function(result) {
						that.stat = result.stat;
						that.store = result.store;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>

</style>


